.wc {
  padding-bottom: 57px;
  // background: url("../img/backgrounds/bottom-bg-blur.png") center 100% no-repeat;

  @include vp-767 {
    margin-bottom: 21px;
  }

  b {
    color: $color-accent;
  }
}

.wc-intro {
  position: relative;
  display: flex;
  background: url("../img/backgrounds/top-bg-blur.png") center 100% no-repeat;

  @include vp-767 {
    background: url("../img/backgrounds/top-bg-blur.png") center 80% no-repeat;
  }
}

.wc-intro div.wc-intro-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 130px;
  padding-bottom: 125px;

  @include vp-767 {
    padding-top: 118px;
    padding-bottom: 83px;
  }
}

.wc-intro__heading {
  text-align: center;

  p {
    @include font-settings ($ff-ttt, 700, 24px, 1);
    color: $color-accent;
    text-transform: uppercase;
    margin-bottom: 20px;

    @include vp-767 {
      font-size: 16px;
      margin-bottom: 6px;
    }
  }

  h2 {
    @include font-settings ($ff-ttt, 700, 50px, 1);
    text-transform: uppercase;
    margin-bottom: 66px;
  }
}

.wc-intro__logo {
  width: 424px;
  height: 65px;
  margin: 0 auto;
  margin-bottom: 63px;

  @include vp-767 {
    width: 278px;
    height: 41px;
    margin-bottom: 53px;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.wc-intro__content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 245px;

  @include vp-767 {
    margin-bottom: 81px;
  }
}

.wc-intro__circle {
  border-radius: 50%;
  background-color: $color-golden-puppy-7;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    object-fit: contain;

    @include vp-767 {
      transform: scale(0.75);
    }
  }

  &--main {
    width: 106px;
    height: 106px;
    background: radial-gradient(88.73% 88.73% at 50.29% 11.27%, $color-sweet-corn 0%, $color-accent 100%);
    margin: 0 48px;

    @include vp-767 {
      width: 42px;
      height: 42px;
      margin: 0 16px;
    }
  }

  &--l {
    width: 90px;
    height: 90px;
    margin: 0 21px;

    @include vp-767 {
      width: 35px;
      height: 35px;
      margin: 0 10px;
    }
  }

  &--m {
    width: 66px;
    height: 66px;

    @include vp-767 {
      width: 26px;
      height: 26px;
    }
  }

  &--s {
    width: 55px;
    height: 55px;
    margin: 0 32px;

    @include vp-767 {
      width: 22px;
      height: 22px;
      margin: 0 10px;
    }
  }
}

.wc-intro__decor {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background: radial-gradient(88.73% 88.73% at 50.29% 11.27%, rgba(254, 230, 126, 0.4) 0%, rgba(252, 205, 3, 0.4) 100%);
  opacity: 0.1;
  backdrop-filter: blur(13.5);
  z-index: -1;

  &--1 {
    width: 898px;
    height: 898px;
    top: -92px;

    @include vp-767 {
      width: 374px;
      height: 374px;
      top: 68px;
    }
  }

  &--2 {
    width: 723px;
    height: 723px;
    top: -5px;

    @include vp-767 {
      width: 282px;
      height: 282px;
      top: 115px;
    }
  }

  &--3 {
    width: 538px;
    height: 538px;
    top: 87px;

    @include vp-767 {
      width: 210px;
      height: 210px;
      top: 150px;
    }
  }

  &--4 {
    width: 333px;
    height: 333px;
    top: 190px;

    @include vp-767 {
      width: 130px;
      height: 130px;
    }
  }

  &--5 {
    width: 185px;
    height: 185px;
    top: 265px;

    @include vp-767 {
      width: 72px;
      height: 72px;
      top: 220px;
    }
  }
}

.wc-intro__down {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  margin: 0 auto;
  z-index: 100;

  svg {
    transform: rotate(180deg);

    @include vp-767 {
      transform: rotate(180deg) scale(0.65);
    }
  }
}

.wc-description {
  .container {
    position: relative;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 44px, 1.1);
    max-width: 55%;

    @include vp-1279 {
      font-size: 36px;
    }

    @include vp-767 {
      min-width: 100%;
      font-size: 21px;
    }
  }

  .wc-description__image {
    position: absolute;
    z-index: -1;

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }
}

.wc-description-primary {
  .container {
    padding-top: 50px;
    padding-bottom: 230px;

    @include vp-767 {
      padding-top: 0;
      padding-bottom: 341px;
    }
  }

  .wc-description__image {
    &--1 {
      width: 461px;
      height: 296px;
      top: 7px;
      right: 97px;

      @include vp-1279 {
        width: 266px;
        height: auto;
      }

      @include vp-767 {
        top: 169px;
        left: 23px;
      }
    }

    &--2 {
      width: 401px;
      height: 324px;
      top: 104px;
      right: 105px;

      @include vp-1279 {
        width: 233px;
        height: auto;
        top: 66px;
      }

      @include vp-767 {
        top: 224px;
        left: 52px;
      }
    }
  }
}

.wc-description-secondary {
  .container {
    padding-top: 118px;
    padding-bottom: 142px;

    @include vp-767 {
      padding-top: 0;
      padding-bottom: 216px;
    }
  }

  h2 {
    margin-left: auto;
  }

  .wc-description__image {
    &--1 {
      width: 527px;
      height: 526px;
      top: -23px;
      left: -23px;
      z-index: -1;

      @include vp-1279 {
        width: 311px;
        height: auto;
        top: 46px;
      }

      @include vp-767 {
        top: 111px;
        left: -18px;
      }
    }

    &--2 {
      width: 289px;
      height: 294px;
      top: 194px;
      left: 239px;
      z-index: -2;

      @include vp-1279 {
        width: 169px;
        height: auto;
        top: 164px;
        left: 229px;
      }

      @include vp-767 {
        top: 238px;
        left: 136px;
      }
    }
  }
}

.wc-signup {
  p {
    @include font-settings ($ff-inter, 500, 14px, 1.1);

    @include vp-767 {
      font-size: 14px;
      line-height: 1.2;
      padding: 0 40px;
    }
  }

  .wc-signup__heading {
    margin-right: 200px;

    @include vp-1279 {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }
}

.wc-signup__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 27px 51px;
  background: rgba(255, 229, 138, 0.07);
  backdrop-filter: blur(17px);
  border-radius: 20px;
  border: 1px solid $color-accent-18;

  @include vp-1279 {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  @include vp-767 {
    padding: 19px 15px;
  }
}

.wc-signup__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 57px;
  height: 57px;
  background: $color-black-40;
  border-radius: 5px;

  @include vp-767 {
    display: none;
  }
}

.wc-signup__heading {
  max-width: 410px;
  margin-right: 24px;

  @include vp-767 {
    margin-bottom: 22px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 36px, 1);
    margin-bottom: 9px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 4px;
    }
  }
}

.wc-btn {
  @include font-settings ($ff-inter, 500, 19px, 1.43);
  color: $color-maire2;
  background-color: $color-accent;
  display: inline-flex;
  letter-spacing: 0.14px;
  padding: 14px 33px;
  border: none;
  border-radius: 14px;
  will-change: background-color;
  transition: background-color $trans-default;

  &:hover {
    background-color: $color-olive;
  }
}

.wc-info {
  background:
    url("../img/backgrounds/info-bg-blur-1.png") 50% 20% no-repeat,
    url("../img/backgrounds/info-bg-blur-2.png") 30% 160% no-repeat;

  @include vp-767 {
    background:
      url("../img/backgrounds/info-bg-blur-1.png") center 16% no-repeat,
      url("../img/backgrounds/info-bg-blur-2.png") center 124% no-repeat;
  }
}

.wc-info__wrapper {
  position: relative;
  display: flex;
  padding-top: 292px;
  padding-bottom: 266px;

  @include vp-767 {
    padding-top: 78px;
  }
}

.wc-info__image {
  position: absolute;
  z-index: -1;

  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }

  &--1 {
    width: 761px;
    height: 799px;
    top: 5px;
    left: -150px;

    @include vp-767 {
      width: 431px;
      top: 116px;
      left: -79px;
    }
  }

  &--2 {
    width: 245px;
    height: 246px;
    top: 446px;
    left: 290px;

    @include vp-767 {
      width: 143px;
      top: 365px;
      left: 159px;
    }
  }
}

.wc-info__heading {
  max-width: 45%;
  margin: 0 40px 0 auto;

  @include vp-767 {
    max-width: 100%;
    margin: 0;
  }

  h2 {
    margin-bottom: 16px;

    @include vp-767 {
      margin-bottom: 132px;
    }

    span {
      @include font-settings ($ff-ttt, 800, 90px, 1);
      display: inline-block;
      text-transform: uppercase;

      @include vp-1279 {
        font-size: 48px;
      }
    }
  }

  span {
    @include font-settings ($ff-inter, 500, 24x, 1.1);
  }
}


.wc-info__text {
  display: flex;
  align-items: center;
}

.wc-info__btn {
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
}

.wc-info__item {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 284px;
  background: $color-vis-vis-7;
  border-radius: 20px;
  border: 1px solid $color-accent-18;
  margin-bottom: 51px;

  &--left {
    padding: 10px 252px 10px 39px;
  }

  &--right {
    padding: 10px 38px 10px 270px;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 10px 15px 20px;
    margin-bottom: 63px;
  }
}

.wc-info__counter {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 121px;
  height: 121px;
  margin-right: 30px;
  background-color: $color-black-40;
  border-radius: 20px;
  user-select: none;

  @include vp-767 {
    min-width: 41px;
    height: 41px;
    border-radius: 7px;

    &--1 {
      margin-bottom: 166px;
    }

    &--2 {
      margin-bottom: 235px;
    }

    &--3 {
      margin-bottom: 267px;
    }

    &--4 {
      margin-bottom: 152px;
    }

    &--5 {
      margin-bottom: 126px;
    }

    &--6 {
      margin-bottom: 184px;
    }
  }


  span {
    @include font-settings ($ff-ttt, 700, 90px, 1);
    display: block;

    @include vp-767 {
      font-size: 29px;
    }
  }
}

.wc-info__content {
  h3 {
    @include font-settings ($ff-inter, 700, 44px, 1.1);
    margin-bottom: 15px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 9px;
    }
  }

  p {
    @include font-settings ($ff-inter, 300, 16px, 1.5);

    @include vp-767 {
      font-size: 12px;
    }
  }
}

.wc-info__link {
  @include font-settings ($ff-inter, 500, 14px, 1);
  color: $color-white-opacity-50;
  display: inline-block;
  text-align: center;
  position: relative;
  border: 1px solid $color-white-opacity-50;
  border-radius: 10px;
  margin-top: 15px;
  padding: 13px;
  z-index: 100;
  will-change: background-color;
  transition: background-color 0.3s ease-in;

  &:hover {
    background-color: rgba(255, 255, 255, 0.06);
    border: 1px solid $color-default-white;
  }
}

.wc-info__decor-image {
  position: absolute;

  img {
    object-fit: contain;
  }

  &--1 {
    width: 598px;
    height: 430px;
    top: -48px;
    right: -165px;

    @include vp-767 {
      width: 478px;
      height: 344px;
      top: -121px;
      right: -108px;
    }
  }

  &--2 {
    width: 471px;
    height: 471px;
    top: -92px;
    left: -113px;

    @include vp-767 {
      width: 424px;
      height: 424px;
      top: -117px;
      right: -92px;
      left: auto;
    }
  }

  &--3 {
    width: 232px;
    height: 381px;
    top: -53px;
    right: 0;

    @include vp-767 {
      top: -60px;
      right: 19px;
    }
  }

  &--4 {
    width: 338px;
    height: 368px;
    top: -40px;
    left: -52px;

    @include vp-767 {
      width: 237px;
      height: 258px;
      top: -86px;
      right: 25px;
      left: auto;
    }
  }

  &--5 {
    width: 569px;
    height: 569px;
    top: -156px;
    right: -188px;

    @include vp-767 {
      width: 392px;
      height: 392px;
      transform: rotate(20deg);
      top: -207px;
      right: -123px;
    }
  }

  &--6 {
    width: 409px;
    height: 410px;
    top: -56px;
    left: -114px;

    @include vp-767 {
      width: 323px;
      height: 324px;
      top: -102px;
      right: -51px;
      left: auto;
    }
  }
}

.wc-partners {
  .container {
    padding-top: 68px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;

    @include vp-767 {
      padding-top: 55px;
      padding-bottom: 20px;
    }
  }

  h2 {
    @include font-settings ($ff-ttt, 700, 36px, 1);
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 33px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 38px;
    }
  }
}

.wc-partners__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -15px;
  margin-right: -15px;
}

.wc-partners__item {
  margin: 0 15px 20px;

  @include vp-767 {
    margin-bottom: 27px;
  }
}

.wc-partners__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 160px;

  @include vp-767 {
    width: 130px;
  }

  img {
    width: 100%;
    object-fit: contain;
  }

  &:hover {
    transform: scale(1.1);
  }
}

.wc-contacts {
  text-align: center;

  .container {
    padding-top: 123px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 44px, 1.1);
    margin-bottom: 14px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 12px;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 24px, 1.1);
    color: $color-storm-grey;
    margin-bottom: 37px;

    @include vp-767 {
      font-size: 16px;
      margin-bottom: 14px;
    }
  }

  .wc-contacts__btn {
    @include font-settings ($ff-inter, 500, 14px, 1.43);
    letter-spacing: 0.1px;
    padding: 10px 24px;
    background-color: transparent;
    border: 1px solid $color-storm-grey;
    border-radius: 10px;
    margin-bottom: 92px;
    cursor: pointer;

    @include vp-767 {
      margin-bottom: 81px;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.06);
      border: 1px solid $color-default-white;
    }
  }
}

.wc-signup-bottom {
  .wc-signup__heading {
    margin-left: 20px;

    @include vp-767 {
      margin-left: 0;
    }

    h2 {
      @include vp-767 {
        margin-bottom: 15px;
      }
    }
  }
}

.wc-socials__list {
  display: flex;
  flex-wrap: wrap;

  @include vp-767 {
    margin-right: -10px;
  }
}

.wc-socials__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33px;
  height: 33px;
  border-radius: 11px;
  background: $color-black-russian;
  backdrop-filter: blur(4.5px);
  margin: 0 8px 10px 0;

  &:hover {
    background-color: $color-black-russian-50;
  }

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }

  @include vp-767 {
    width: 49px;
    height: 49px;
    margin: 0 10px 10px 0;

    svg {
      width: 24px;
      height: 24px;
    }
  }
}

.wc-socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
