.main {
  z-index: 100;

  &.main-grid {
    .container {
      display: grid;
      grid-template-columns: auto 295px;
      column-gap: 20px;
      margin-top: 25px;
      padding-bottom: 25px;
      background-color: #14161B;

      @include vp-1279 {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
        row-gap: 20px;
      }

      @include vp-767 {
        row-gap: 17px;
        margin-top: 20px;
      }
    }
  }

  &.main-block {
    .container {
      margin-top: 28px;
      padding-bottom: 25px;
      background-color: #14161B;

      @include vp-1279 {
        display: flex;
        flex-direction: column;
      }

      @include vp-767 {
        margin-top: 20px;
      }
    }
  }
}

.main-content {
  display: flex;
  flex-direction: column;
}
